@import url(https://fonts.googleapis.com/css2?family=Sansita+Swashed&family=Work+Sans:wght@400;500;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Yellowtail&display=swap);
:root{  
  --background_basic_color:#E8E5DA;
  --white:#fff;
  --primary_color: #DC143C;
  --secondary_color:#000;
  --font_weight_bold:900;
  --font_weight_medium:700;
  --font_weight_regular:500;
  --font_family:'Work Sans', sans-serif;
}

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  overflow-x: hidden;
}
body{
  font-family: 'Work Sans', sans-serif;
  font-family: var(--font_family);
}
/* Sidebar */
.sidepart{
  width: 20%;
  height: 100vh;  
  position: fixed;
  left: 0;
  font-family: 'Work Sans', sans-serif;
  background-color: #000;
  background-color: var(--secondary_color);
  color:#fff;  
  transition: all .7s;
  z-index: 1;
}

.sidepart.active{
  margin-left: -16%;
}

.img-profile{
  height: 9rem;
  width: 9rem;
  background-image: url(/ReactWebsite/static/media/profile2.e786e170.png);  
  background-size:cover;
  background-color: #E8E5DA;
  background-color: var(--background_basic_color);
  border-radius: 50%;
  margin-left: auto;
  margin-right: auto;
  display: block;  
  margin-top: 15%;
}
.name{  
  text-align: center;
  margin-top: 2%;  
}
.work{
  text-align: center;
  font-weight: 500;
  margin-top: -3%;
}
.sidebar{
  display: flex;  
  flex-direction: column;  
  left: 0;
  position: absolute;
  width: 100%;
  /* text-align: center;     */
}
.sidebar i{  
  float: right; 
  z-index: 11; 
  font-size: 1.5rem;      
  object-fit: cover;  
  margin-top: 2.5%;
}
.sidebar .sidebar-item{  
  margin-top: 5%;  
  min-width: 100%;
  font-size: 1.1rem;  
  font-weight: 500;   
  text-decoration: none;
  line-height: 38px;
  transition: 0.6s;
  color: #fff;
  color: var(--white);  
  margin-left: 2%;  
  padding-right: 8%;  
  padding-left: 3%;
  text-decoration: none;
}
.sidebar .sidebar-item:hover{
  background-color: #DC143C;
  background-color: var(--primary_color);  
  color: #fff;  
  color: var(--white);
  width: 100%;
  text-decoration: none;  
  /* border-radius: 10px; */
}
.sidebar .sidebar-item.active2{
  background-color: #DC143C;
  background-color: var(--primary_color);
}
.fa-arrow-circle-right{
  color: #fff;
  color: var(--white);
  float: right;
  font-size: 2rem;
  margin-top: 2%;  
  z-index: 13;  
  padding-right: 5%;  
}
.fa-arrow-circle-left{
  color: #fff;
  color: var(--white);
  float: right;
  font-size: 2rem;
  margin-top: 2%;  
  z-index: 13;
  padding-right: 3%;  
}

/* home page */
.container-home{
  width: 96%;  
  right: 0;
  height: 100vh;     
  position: absolute;
  background-image: url(/ReactWebsite/static/media/b1.c85baa9c.jpg);
  background-size: cover;
  transition: all .7s;
}
.container-home .overlay{  
  width:100%;
  text-align:center;  
  padding:2px;  
  padding: 0% 10% 0% 10%;             
  background: rgba(255, 255, 255, 0.5);
}
.container-home .overlay .content{ 
  color: #000; 
  color: var(--secondary_color);   
  z-index: 1;
  font-family: 'Work Sans', sans-serif;
  text-align: center;
}
.container-home Button{
  border-radius: 30px;  
  background-color: #DC143C;  
  background-color: var(--primary_color);
  color: #fff;
  color: var(--white);
  padding: 1% 3% 1% 3%;
  font-weight: 500;
  font-size: 1.1rem;
  letter-spacing: 1px;
}
.container-home Button:hover{
  background-color: #000;
  background-color: var(--secondary_color);
  color: #fff;
  color: var(--white);
}
.container-home .overlay .content h1{
  font-size: 4rem;
  font-weight: 600;
  letter-spacing: 2px;
}
.container-home .overlay .content .seco{
  font-size: 1.5rem;
  font-family: 'Yellowtail', cursive;
  letter-spacing: 3px;
  font-weight: 600;
}
.container-home .overlay .content .thir{
  font-family: 1.2rem;
  text-align: justify;
  font-weight: 500;
}

/* Heading */
.container-1{
  width: 50%;
  text-align: center;
  margin-left: auto;
  margin-right: auto;
  margin-top: 5%;
  color: #000;
  color: var(--secondary_color);
  font-family: 'Work Sans', sans-serif;
}

.container-1 .line{  
  height: 2px;
  width: 4rem;  
  background-color: #DC143C;  
  background-color: var(--primary_color);
  margin-left: auto;
  margin-right: auto;
}
.container-1 p{
  margin-top: 5%;
}
/* services */
.container-service{
  width: 96%;  
  height: 100vh;            
  right: 0;
  position: fixed;
  font-family: 'Work Sans', sans-serif;
  background-color: #E8E5DA;
  background-color: var(--background_basic_color);  
  transition: all .7s;
}
.fa-desktop{
  font-size: 3.5rem;
  text-align: center;
  color: #DC143C;
  color: var(--primary_color);
}
.fa-mobile{
  font-size: 3.5rem;
  text-align: center;
  color: #DC143C;
  color: var(--primary_color);
}
.fa-code{
  font-size: 3.5rem;
  text-align: center;
  color: #DC143C;
  color: var(--primary_color);
}
.container-service .card{  
  height: 18.5rem;
  padding-top: 10%;
  text-align: center;
  color: #fff;
  color: var(--white);      
  margin-top: 20%;      
  box-shadow: 1px 1px 4px 2px #000;      
  box-shadow: 1px 1px 4px 2px var(--secondary_color);  
  margin-bottom: 2%;
}
.container-service .card h4{
  padding-top: 10%;  
}
.container-service .card p{
  text-align: center;
}


/* about */
.container-about{
  width: 96%;  
  height: 100vh;  
  padding-left: 2%;           
  right: 0;
  position: absolute;
  font-family: 'Work Sans', sans-serif;
  background-color: #E8E5DA;
  background-color: var(--background_basic_color);  
  transition: all .7s;
}
.container-about .left{
  margin-top: 2%;          
}
.container-about .right{
  margin-top: 8%;  
  width: 10rem;  
  height: 100%;
  padding: 2% 5% 2% 2%;
}
.container-about .right h4{
  font-size: 1.1rem;
  color: #DC143C;
  color: var(--primary_color);   
  height: 1.4rem; 
}

/* skills */
.container-skill{
  width: 96%;      
  height: 100vh;  
  right: 0px;
  position: absolute;
  transition: 0.7s all;
  background: #E8E5DA;
  background: var(--background_basic_color);
}
.container-skill .row{
  margin-top: 5%;  
}
.container-skill .left{
  padding: 1% 7% 0% 5%;
}
.container-skill .left .title{
  color: #DC143C;
  color: var(--primary_color);
  font-weight: 500;  
}
.container-skill .left h3{
  height: 2.5rem;  
  font-size: 2rem;
  font-weight: 700;
}
.container-skill .left .inn-content{  
  font-size: 1.1rem;
  font-weight: 500;
}
.container-skill .left Button{
  border:2px solid #DC143C;
  border:2px solid var(--primary_color);
  border-radius: 30px;
  font-weight: 500;
  padding: 2% 5% 2% 5%;
  letter-spacing: 1px;
}
.container-skill .left Button:hover{
  background-color: #DC143C;
  background-color: var(--primary_color);
  color: #fff;
}
.container-skill.right{
  display: flex;  
  flex-direction: column;
}
.container-skill .right label{
  font-size: 1.1rem;
  margin-left: 5%;
  font-weight: 500;
  letter-spacing: 2px;
}
.container-skill .right .progress{
  background-color: #000;
  background-color: var(--secondary_color);
  color: #fff;
  color: var(--white);
  height: 2rem;  
  border-radius: 1rem;
  width: 22rem;  
  margin-top: 5%;
}
.container-skill .right .progress .progress-inn{
  background-color: #DC143C;
  background-color: var(--primary_color);
  width: 0%;
  border-radius: 2rem;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: 700;
  font-size: 1.1rem;    
}
.container-skill .right label{
  margin-top: 5%;
  text-decoration: underline;
  font-weight: 700;
}

/* portfolio */
.container-portfolio{
  width: 96%;
  height: 100vh;
  right: 0;
  position: absolute;
  background: #E8E5DA;
  background: var(--background_basic_color) ;
}

/* contact */
.container-contact{
  width: 96%;
  height: 100vh;
  right: 0;
  position: absolute;
  background: #E8E5DA;
  background: var(--background_basic_color) ;
  transition: .7s;
}
.container-contact .container{
  padding: 2% 5%;
}
.container-contact .container Label{
  line-height: 1.4em;
  font-weight: 500;
  font-weight: var(--font_weight_regular);
}
.container-contact .container input{
  height: 2.4em;
  width: 90%;
  margin-bottom: 3%;  
  padding-left: 2%;  
  border-top: none;
  border-left: none;
  border-right: 4px solid #DC143C;
  border-right: 4px solid var(--primary_color);;
  border-bottom: none;
  outline: none;  
  border-radius: 0.5em ;
}
.container-contact .container textarea{  
  padding-left: 2%;
  padding-top: 1%;
  outline: none;
  width: 90%;
}
.container-contact .container .right{
  padding: 0 0 0 8%;
}
.container-contact .container .detail{
    font-size: 1.5rem;    
    padding-top: 5%;    
}
.container-contact .container .detail .row{
  padding-bottom: 5%;
}
.container-contact .container .account .git,.twitt,.insta{
  height: 3rem;
  width: 3rem;
  border-radius: 50%;
  text-align: center;
  color: #fff;
  color: var(--white);
  margin-top: 8%;
  margin-right: 15%;
  background:#DC143C;
  background:var(--primary_color) ;
  transition: .5s;
}
.container-contact .container .account .insta:hover{
  background: #000;
  background: var(--secondary_color);
  cursor: pointer;
}
.container-contact .container .account .twitt:hover{
  background: #000;
  background: var(--secondary_color);
  cursor: pointer;
}
.container-contact .container .account .git:hover{
  background: #000;
  background: var(--secondary_color);
  cursor: pointer;
}
.container-contact .container .account{
  font-size: 1.5rem;
}
.container-contact .btn{
  width: 10rem;
  background-color: #DC143C;
  background-color: var(--primary_color);
  color: #fff;
  color: var(--white);
  margin-top: 2%;
}
.container-contact .btn:hover{
  background-color: #000;
  background-color: var(--secondary_color);
  color: #fff;
  color: var(--white);  
}
@media screen and (max-width:767px){
    .sidepart{      
      width: 100%;
      height: 7vh;
      position: fixed;      
      padding: 0;
      margin: 0;
      z-index: 1; 
      background-color: rgba(0,0,0,0.9);     
      transition: .5s;
    }        
    .sidepart.active{      
      padding: 0;
      margin: 0;
      height: 100vh;
    }
    .container-home{
        width: 100%;                
        height: 100vh;        
        padding: 0;
        z-index:-1;
        position: fixed;
        background-size: 100% 100%;        
      }
      .overlay{  
        height: 100%;                        
        width: 100%;
        margin: 0% auto;                
        display: flex;
        justify-content: center;
        align-items: center;
        opacity:0.8;  
        background-color: #000;  
      }
      .overlay .content{ 
        color: #fff;                 
      }
      .overlay .content h1{
        font-size: 2rem;        
      }
      .overlay .content .seco{
        font-size: 1.2rem;        
      }
      .overlay .content .thir{
        font-size: 1.2rem;        
      }
      .container-home .overlay .content h1{
        font-size: 2.5rem;
        font-weight: 600;
        letter-spacing: 2px;
      }
      .container-home .overlay .content .seco{
        font-size: 1rem;
      }
      .container-home .overlay .content .thir{        
        font-size: 1rem;
        text-align: justify;
        font-weight: 500;
      }
      .container-home Button{
        padding: 4% 6% 4% 6%;
        font-size: 1rem;
      }

      .container-1{
        margin-top: 30%;
        width: 80%;
      }
      .container-1 p{
        font-size: 0.7rem;        
      }
      .container-service{
        width: 100%;
      }
      .container-about{
        width: 100%;
      }
      .container-about .left img{
        height: 65%;
      }
      .container-about .right{
        max-width: 100%;
        margin-top: -30%;        
        word-wrap: break-word;
        font-size: 0.8rem;
        padding: 0 10% 0 10%;
        text-align: justify;
      }
      .container-about .right h4{
        font-size: 1rem;
      }      
      .container-about .right .inright{
        margin-left: -10%;
      }

      .container-skill{
        width: 100%;
        text-align: center;
      }      
      .container-skill .left h3{
        font-size: 1.5rem;
      }
      .container-skill .left .inn-content{
          font-size: 0.8rem;
      }
      .container-skill .left Button{
          font-size: 0.9rem;
      }
      .container-skill .right{
        margin-top: 10%;
        padding: 0 10% 0 10%;
      }
      .container-skill .right .progress{
        width: 13rem;
        height: 1.5rem;
        margin-bottom: 5%;
      }
      .container-skill .right .progress .progress-inn{

      }
      .container-skill .right label{
          font-size: 0.7rem;
          margin-left: 4%;
      }
      .container-contact{
        width: 100%;  
      }
      .container-contact .container .right{                
        padding: 10% 0% 0% 15%;        
        width: 100%;
      }
      .container-contact .container .detail{
        font-size: 1rem;
      }
      
} 

